* {
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}


input{
  font-size:1.2rem ;
  width: 100%;

}

.card-grid{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 250px;
  margin-bottom: 25px;

}

.card p{
  padding: 5px;
  text-align: center;
}


.card img{
  height: 200px;
  width: 100%;
}


